import React from "react";

function FRNLineRow(props) {
  var formatter = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD"
  });

  return (
    <tr valign={"top"}>
      <td
        style={
          ({ border: "1px solid black" },
          { paddingLeft: "10px" },
          { paddingRight: "10px" })
        }
      >
        {props.line.form_471_line_item_number}
      </td>
      <td
        style={
          ({ border: "1px solid black" },
          { paddingLeft: "5px" },
          { paddingRight: "5px" })
        }
      >
        {props.line.model_of_equipment}
      </td>
      <td
        style={
          ({ border: "1px solid black" },
          { paddingLeft: "5px" },
          { paddingRight: "5px" },
          { textAlign: "right" })
        }
      >
        {formatter.format(props.line.total_one_time_cost)}
      </td>
      <td
        style={
          ({ border: "1px solid black" },
          { paddingLeft: "5px" },
          { paddingRight: "5px" },
          { textAlign: "right" })
        }
      >
        {formatter.format(props.line.one_time_ineligible_cost)}
      </td>
      <td
        style={
          ({ border: "1px solid black" },
          { paddingLeft: "5px" },
          { paddingRight: "5px" },
          { textAlign: "right" })
        }
      >
        {formatter.format(props.line.one_time_eligible_costs)}
      </td>
      <td
        style={
          ({ border: "1px solid black" },
          { paddingLeft: "5px" },
          { paddingRight: "5px" },
          { textAlign: "center" })
        }
      >
        {props.line.one_time_quantity}
      </td>
      <td
        style={
          ({ border: "1px solid black" },
          { paddingLeft: "5px" },
          { paddingRight: "5px" },
          { textAlign: "right" })
        }
      >
        {formatter.format(props.line.total_eligible_one_time_costs)}
      </td>
    </tr>
  );
}

export default FRNLineRow;
