import React from "react";
import FRNLineRow from "./FRNLineRow";

function FRNDetails(props) {
  if (props.data.length !== 0) {
    return (
      <div>
        <table
          style={
            ({ border: "1px solid black" }, { borderCollapse: "collapse" })
          }
        >
          <tr valign={"bottom"}>
            <th style={{ border: "1px solid black" }}>FRN Line</th>
            <th width={"512px"} style={{ border: "1px solid black" }}>
              Model
            </th>
            <th style={{ border: "1px solid black" }}>Cost</th>
            <th style={{ border: "1px solid black" }}>Ineligible</th>
            <th style={{ border: "1px solid black" }}>Eligible</th>
            <th style={{ border: "1px solid black" }}>Qty</th>
            <th style={{ border: "1px solid black" }}>Tot Eligible</th>
          </tr>
          {props.data.map((createRow) => (
            <FRNLineRow line={createRow} />
          ))}
        </table>
      </div>
    );
  } else {
    return <div />;
  }
}

export default FRNDetails;
