import React from "react";

function FRNHeading(props) {
  var formatter = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD"
  });

  return (
    <div>
      <h3>
        {props.headObj.funding_request_number} - {props.headObj.nickname}
      </h3>
      Service Provider: {props.headObj.spin_name}
      <br />
      Application Number: {props.headObj.application_number}
      <br />
      Applicant Name: {props.headObj.organization_name}
      <br />
      FRN Status: {props.headObj.form_471_frn_status_name}
      <br />
      Contract: {props.headObj.contract_number}
      <br />
      Svc Delivery Deadline: {props.headObj.service_delivery_deadline}
      <br />
      Contract Expiration: {props.headObj.contract_expiration_date}
      <br />
      Expiration Date - all Extensions: {props.headObj.extended_expiration_date}
      <br />
      Eligible Amt: {formatter.format(props.headObj.total_pre_discount_costs)}
      <br />
      Discount %: {props.headObj.dis_pct}
      <br />
      Funded Amt: {formatter.format(props.headObj.funding_commitment_request)}
      <br />
      Last Day to Invoice: {props.headObj.last_date_to_invoice}
      <br />
      Disbursement Total:{" "}
      {formatter.format(props.headObj.total_authorized_disbursement)}
      <br /> <br />
    </div>
  );
}

export default FRNHeading;
